<template>
          <section id="facts">
      <div class="container" data-aos="fade-up">

        <header class="section-header">
          <h3>OBIC COIN </h3>
        </header>

        <div class="row counters">

          <div class="col-lg-3 col-6 text-center">
            <span >{{price}} $</span>
            <p>Price</p>
          </div>

          <div class="col-lg-3 col-6 text-center">
            <span >{{market_cap}} $</span>
            <p>Market Cap </p>
          </div>

          <div class="col-lg-3 col-6 text-center">
            <span >{{usd_24h_vol}} $ </span>
            <p>24H Volume</p>
          </div>

          <div class="col-lg-3 col-6 text-center">
            <span >{{usd_24h_change}} %</span>
            <p>24H Change</p>
          </div>

        </div>

     <!--   <div class="facts-img">
          <img src="assets/img/facts-img.png" alt="" class="img-fluid">
        </div> -->

      </div>
    </section>
</template>
<script>
import axios from 'axios';
export default {
  data(){
    return{
      price : 0,
      market_cap : 0,
      usd_24h_vol: 0,
      usd_24h_change: 0
    }
  },
  created(){
    this.registro();
  },
  methods:{
      registro(){
      let me = this;
      let valor = 0;
      axios.get("https://api.coingecko.com/api/v3/simple/price?ids=obic&vs_currencies=usd&include_market_cap=true&include_24hr_vol=true&include_24hr_change=true")
      .then( function(res){
        console.log(res);
        const obic =  res.data.obic
          me.price = obic.usd
            me.market_cap = obic.usd_market_cap
            me.usd_24h_vol = obic.usd_24h_vol.toFixed(2);
            me.usd_24h_change = obic.usd_24h_change.toFixed(2);
        }).catch(function(err){
          console.log(err);
        });
      }
  }

  
}
</script>